"use client";

import { motion, AnimatePresence } from "framer-motion";
import { Connector, useConnect } from "wagmi";
import { X, Wallet, ArrowRight } from "lucide-react";
import { Button } from "@/components/ui/button";

interface WalletModalProps {
    isOpen: boolean;
    onClose: () => void;
}

export function WalletModal({ isOpen, onClose }: WalletModalProps) {
    const { connectors, connect, isPending } = useConnect();

    // Filter out duplicates if any (e.g. injected vs metamask)
    // We want to show unique options.
    // Strategy: 
    // 1. Explicitly show popular ones if they exist (MetaMask, Coinbase, Safe)
    // 2. Show 'Injected' if it's not one of the above
    // 3. Show others

    const uniqueConnectors = connectors.filter((c, index, self) =>
        index === self.findIndex((t) => t.id === c.id)
    );

    const handleConnect = (connector: Connector) => {
        connect({ connector }, {
            onSuccess: () => onClose(),
            onError: (err) => alert(err.message)
        });
    };

    return (
        <AnimatePresence>
            {isOpen && (
                <>
                    {/* Backdrop */}
                    <motion.div
                        initial={{ opacity: 0 }}
                        animate={{ opacity: 1 }}
                        exit={{ opacity: 0 }}
                        onClick={onClose}
                        className="fixed inset-0 bg-black/60 backdrop-blur-sm z-50"
                    />

                    {/* Modal */}
                    <motion.div
                        initial={{ opacity: 0, scale: 0.95, y: 20 }}
                        animate={{ opacity: 1, scale: 1, y: 0 }}
                        exit={{ opacity: 0, scale: 0.95, y: 20 }}
                        className="fixed left-1/2 top-1/2 -translate-x-1/2 -translate-y-1/2 w-full max-w-md bg-background border rounded-3xl shadow-2xl z-50 overflow-hidden"
                    >
                        <div className="p-6 border-b flex items-center justify-between">
                            <h2 className="text-xl font-bold flex items-center gap-2">
                                <Wallet className="w-5 h-5 text-primary" />
                                Connect Wallet
                            </h2>
                            <Button variant="ghost" size="icon" onClick={onClose} className="rounded-full hover:bg-muted">
                                <X className="w-5 h-5" />
                            </Button>
                        </div>

                        <div className="p-4">
                            <p className="text-sm text-muted-foreground mb-4 px-2">
                                Choose a wallet to connect to ChainTicket+.
                            </p>

                            <div className="space-y-2 max-h-[300px] overflow-y-auto pr-2 custom-scrollbar">
                                {uniqueConnectors.map((connector) => (
                                    <button
                                        key={connector.uid}
                                        onClick={() => handleConnect(connector)}
                                        disabled={isPending}
                                        className="w-full flex items-center justify-between p-4 rounded-xl border hover:border-primary/50 hover:bg-secondary/50 transition-all group text-left"
                                    >
                                        <div className="flex items-center gap-3">
                                            <div className="w-10 h-10 rounded-full bg-white/10 flex items-center justify-center border">
                                                {/* We could add specific icons here based on connector.id */}
                                                {connector.name === 'MetaMask' ? (
                                                    <div className="w-6 h-6 bg-orange-500 rounded-full" />
                                                ) : connector.name === 'Coinbase Wallet' ? (
                                                    <div className="w-6 h-6 bg-blue-500 rounded-full" />
                                                ) : (
                                                    <Wallet className="w-5 h-5 text-muted-foreground" />
                                                )}
                                            </div>
                                            <div>
                                                <p className="font-semibold">{connector.name}</p>
                                                <p className="text-xs text-muted-foreground capitalize">{connector.id}</p>
                                            </div>
                                        </div>
                                        <ArrowRight className="w-5 h-5 text-muted-foreground group-hover:text-primary transition-colors opacity-0 group-hover:opacity-100" />
                                    </button>
                                ))}

                                {uniqueConnectors.length === 0 && (
                                    <div className="text-center py-8 text-muted-foreground">
                                        No wallets found. Please install a wallet extension.
                                    </div>
                                )}
                            </div>
                        </div>

                        <div className="p-4 border-t bg-muted/30 text-center text-xs text-muted-foreground">
                            By connecting, you agree to our Terms of Service and Privacy Policy.
                        </div>
                    </motion.div>
                </>
            )}
        </AnimatePresence>
    );
}
